html {
  font-size: 1.66667vw;
}

@media (min-width: 1440px) {
  html {
    font-size: 24px;
  }
}

@media (max-width: 768px) {
  html {
    font-size: 3.75vw;
  }
}

.wrap {
  width: 100%;
  margin: 0;
  padding: 0;
}

header {
  min-height: 4.16667rem;
}

footer {
  min-height: 4.16667rem;
}

.container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
}

* {
  margin: 0;
  padding: 0;
}

a {
  color: #000000;
  text-decoration: none;
}

img {
  border: none;
  max-width: 100%;
}

ul {
  list-style: none;
}

.clear {
  clear: both;
}

.clearfix:after {
  display: table;
  content: "";
  line-height: 0;
  clear: both;
}

.tc {
  text-align: center;
}

.tr {
  text-align: right;
}

.tl {
  text-align: left;
}

.fl {
  float: left;
}

.fr {
  float: right;
}

.rel {
  position: relative;
}

.pab {
  position: absolute;
}

.pc-view {
  display: block !important;
}

.mobile-view {
  display: none !important;
}

@media (max-width: 768px) {
  .pc-view {
    display: none !important;
  }

  .mobile-view {
    display: block !important;
  }

  .container {
    font-size: 1.41667rem;
  }
}

body {
  font-family: Helvetica, Arial, "MHeiHK-Medium", "PingFang HK", "SourceHanSansTWHK-Light", "Microsoft JhengHei", "Helvetica Neue", sans-serif;
